<template>
  <div class="main">
    <h1>{{this.$router.history.current.name}}</h1>
    <h2>focus</h2>
    <input v-focus type="text">
    <h2>red</h2>
    <input v-mycss type="text">
    <div class="divBox mt-10">
      <ul class="div inline">
        <h3></h3>
        <li v-for="(hook, index) in hookList" :key="index">
          <p><span :title="hook.describe">{{ hook.name }}</span>: {{ hook.describe.slice(0, 22) + '...' }}</p>
        </li>
        <li>
          <div id="hook-arguments-example" v-demo:foo.a.b="message"></div>
        </li>
      </ul>
      <ul class="div inline">
        <h3></h3>
        <li v-for="(param, index) in paramsList" :key="index">
          <div>
            <p><span :title="param.describe">{{ param.name }}</span>: {{ param.describe.slice(0, 22) + '...' }}</p>
            <ul>
              <li v-for="(item, index) in param.childrens" :key="index">
                <p><span :title="item.describe">{{ item.name }}</span>: {{ item.describe.slice(0, 22) + '...' }}</p>
              </li>
            </ul>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Directives',
  data () {
    return {
      message: 'hello!',
      hookList: [
        {name: 'bind', describe: ''},
        {name: 'inserted', describe: ' ()'},
        {name: 'update', describe: ' VNode  VNode '},
        {name: 'componentUpdated', describe: ' VNode  VNode '},
        {name: 'unbind', describe: ''}
      ],
      paramsList: [
        {name: 'el', describe: ' DOM'},
        {
          name: 'binding',
          describe: '',
          childrens: [
            {name: 'name', describe: ' v- '},
            {name: 'value', describe: 'v-my-directive="1 + 1"  2'},
            {name: 'oldValue', describe: ' update  componentUpdated '},
            {name: 'expression', describe: ' v-my-directive="1 + 1"  "1 + 1"'},
            {name: 'arg', describe: ' v-my-directive:foo  "foo"'},
            {name: 'modifiers', describe: 'v-my-directive.foo.bar  { foo: true, bar: true }'}
          ]
        },
        {name: 'vnode', describe: 'Vue '},
        {name: 'oldVnode', describe: ' update  componentUpdated '}
      ]
    }
  },
  directives: {
    mycss: {
      // 
      inserted: function (el) {
        el.style.color = 'red'
      }
    },
    //     el  dataset 
    // Object.keys() 
    demo: {
      bind: function (el, binding, vnode) {
        let s = JSON.stringify
        el.innerHTML =
          'name: ' + s(binding.name) + '<br>' +
          'value: ' + s(binding.value) + '<br>' +
          'expression: ' + s(binding.expression) + '<br>' +
          'argument: ' + s(binding.arg) + '<br>' +
          'modifiers: ' + s(binding.modifiers) + '<br>' +
          'vnode keys: ' + Object.keys(vnode).join(', ')
      }
    }
  }
}
</script>
<style scoped>
.divBox {
  vertical-align: middle;
  width: 960px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.divBox li {
  text-align: left;
}
.divBox li span {
  cursor: default;
}
.div{
  display: inline-block;
  width: 440px;
  height: 480px;
  border: 1px solid #ddd;
}
.mt-10 {
  margin-top: 10px;
}
</style>



